  .chart-container {
    width: 100%;
    height: 250px;
    margin-bottom: -5px;
    &.full{
      width: 102%;
    }
    &.absolute canvas{
      position: absolute;
      bottom: 0;
    }
  }

  .chart-container-mini {
    width: 100%;
    height: 100px;
    &.full{
      width: 103%;
    }
    &.absolute canvas{
      position: absolute;
      bottom: 0;
    }
  }
.chart-mini{
  overflow: hidden;
  position: relative;
}
  .chart {
    overflow: hidden;
    position: relative;
    &-statistics {
      overflow: hidden;
      .up {
        font-size: 24px;
        line-height: 36px;
        padding: 0 0 0 30px;
        background: url('../img/icon-51.png') no-repeat 0 50%;
      }
      .line {
        font-size: 14px;
        line-height: 36px;
        span {
          padding: 0 0 0 10px;
          font-size: 24px;
        }
      }
    }
    &-stat-1 {
      margin: 20px 0 0;
      .unit {
        width: 33%;
        float: left;
        .h {
          text-transform: uppercase;
          font-size: 16px;
          line-height: 18px;
          margin: 0 0 10px;
        }
        .s {
          font-size: 14px;
          line-height: 18px;
          padding: 0 0 10px;
        }
        img {
          display: block;
          margin: 20px 0 0 10px;
        }
      }
    }
    &-stat-2 {
      margin: 20px 40px;
      .unit {
        width: 33%;
        float: left;
        font-size: 16px;
        line-height: 18px;
        &-group {
          border-bottom: 1px solid $text-dark;
          overflow: hidden;
          clear: both;
          padding: 10px;
        }
      }
    }
    canvas {
      //    display: block;
      //    margin: 0 auto;
      &.view-1 {
        margin: 0 -5px -5px;
        width: calc(100% + 10px);
        height: auto;
      }
      &.view-2 {
        width: 100%;
        height: auto;
      }
      &#chartLine {
        margin: 0 -5px -15px;
        width: calc(100% + 10px);
        height: auto;
      }
      &#chartBar {
        width: 100%;
        height: auto;
      }
      &#chartRadar {
        width: 350px;
        height: 350px;
        @include media('sm') {
          width: 286px;
          height: 286px;
        }
      }
      &#chartArea {
        width: 350px;
        height: 350px;
        @include media('sm') {
          width: 286px;
          height: 286px;
        }
      }
      &#chartPie {
        width: 286px;
        height: 286px;
      }
      &#chartDoughnut {
        width: 286px;
        height: 286px;
      }
    }
  }
