.contacts {

    &-search{
      width: 100%;
      line-height: 38px;
      color: $text-dark;
      padding: 0 8px;
      border: 1px solid #ccc;
      border-right: 0;
      &:-moz-placeholder {
        color: $text-dark;
      }
      &::-moz-placeholder {
        color: $text-dark;
      }
      &:-ms-input-placeholder {
        color: $text-dark;
      }
      &::-webkit-input-placeholder {
        color: $text-dark;
      }
      +.input-group-addon{
        background: #fff;
        border-radius: 0;
        button{
          background: transparent;
        }
      }
    }

  &-labels {

    .settings{
      min-width: 70px;
      max-width: 70px;
      line-height: 38px;
      float: left;
      margin-right: 10px;
    }

    .title {
      font-size: 16px;
      line-height: 112px;
      background: url('../img/icon-42.png') 0 50% no-repeat;
      padding: 0 0 0 32px;
      color: $text-dark;
      overflow: hidden;

      span {
        float: right;
      }
    }

    .list {

      .head {
        font-size: 20px;
        line-height: 32px;
        font-weight: bold;
        text-align: center;
        background: $brand-info;
        color: #ffffff;
        margin: 60px 0 40px;
      }

      .unit {
        overflow: hidden;
        padding: 0 0 20px;

        &:last-of-type {
          margin-bottom: 20px;
        }

        .lab {
          font-size: 16px;
          line-height: 32px;
          float: left;
          width: 112px;
          padding: 0 0 0 18px;
          color: #ffffff;

          &-default {
            background: $brand-default;
          }

          &-primary {
            background: $brand-primary;
          }

          &-success {
            background: $brand-success;
          }

          &-warning {
            background: $brand-warning;
          }

          &-danger {
            background: $brand-danger;
          }
        }

        span {
          display: block;
          float: right;
          font-size: 16px;
          line-height: 32px;
          font-weight: bold;
        }
      }
    }
  }

  &-list {
    border-left: 1px solid $brand-default-hue;
    margin: -20px 0 40px;

    @include media('md') {
      border-left: none;
      margin: 0;
    }

    @media(max-width: 480px) {
      margin: 0 -20px;
    }

    &-search{
      width: 100%;
      line-height: 60px;
      background: $brand-primary-hue;
      font-size: 24px;
      color: #fff;
      padding: 0 20px;
      &:-moz-placeholder {
        color: #fff;
      }
      &::-moz-placeholder {
        color: #fff;
      }
      &:-ms-input-placeholder {
        color: #fff;
      }
      &::-webkit-input-placeholder {
        color: #fff;
      }
      +.input-group-addon{
        background: $brand-primary-hue;
        border-radius: 0;
        border: 0;
        button{
          background: transparent;
        }
      }
    }

    .lab {
          font-size: 14px;
          line-height: 21px;
          float: left;
          min-width: 75px;
          padding: 0 0 0 18px;
          color: #ffffff;
          margin-left: 60px;
          @media(max-width: 480px) {
            margin-left: 0;
          }
          &-default {
            background: $brand-default;
          }

          &-primary {
            background: $brand-primary;
          }

          &-success {
            background: $brand-success;
          }

          &-warning {
            background: $brand-warning;
          }

          &-danger {
            background: $brand-danger;
          }
        }

    .title {
      padding: 0 0 0 20px;
      color: $brand-primary;
      line-height: 124px;
    }

    .icons {
      padding: 0 0 20px 20px;

      .icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-top: 3px;
      }

      .icon-folder {
        background: url('../img/icon-9.png') no-repeat 50% 50%;
      }

      .icon-label {
        background: url('../img/icon-5.png') no-repeat 50% 50%;
      }

      .icon-trash {
        background: url('../img/icon-10.png') no-repeat 50% 50%;
      }
    }

    .unit {
      padding: 20px 0;
      margin-top: -7px;
      border-bottom: 1px solid $brand-default-hue;
      display: inline-block;
      width: 100%;
      font-size: 16px;
      line-height: 40px;
      color: $text-dark;
      @include transition(background, 0.3s);
      &.selected{
        background: #f6f6f6;
      }

      .dropdown-menu{
        border-radius: 0;
        box-shadow: none;
        border-color: #a3afb7;
        min-width: auto;
        li{
          margin: 0;
          a{
            line-height: 32px;
            padding: 0 10px;
            font-size: 16px;
          }
        }
      }

      &.head {
        font-size: 20px;
        color: $brand-primary;
        padding-bottom: 10px;
        overflow: visible;
      }

      .field {
        float: left;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }

      .name {
        width: 40%;
        padding: 0 0 0 80px;
        position: relative;
        @media(max-width: 480px) {
          width: auto;
        }

        .check {
          position: absolute;
          left: 5px;
          top: -8px;
        }

        .avatar {
          display: inline-block;
          width: 40px;
          height: 40px;
          border-radius: 20px;
          margin: 0 16px 0 0;
          vertical-align: top;
          @media(max-width: 480px) {
            display: none;
          }
        }
      }

      .phone {
        width: 30%;
        padding: 0 0 0 20px;
        @media(max-width: 480px) {
          display: none;
        }
      }

      .email {
        width: 30%;
        padding: 0 0 0 20px;
        overflow: visible;
        @media(max-width: 480px) {
          display: none;
        }
        .btn{
          min-width: 38px;
          max-width: 38px;
          font-size: inherit;
          padding: 0 8px;
          line-height: 36px;
        }
      }
    }
  }
}
