.gallery {

  //&-drop {
  //	background: #ffffff;
  //	border: 2px dashed $brand-default;
  //	height: 120px;
  //	line-height: 116px;
  //	font-size: 20px;
  //	color: $text-dark;
  //	margin: 0 0 20px;
  //	text-align: center;
  //	cursor: move;
  //}

  &-main {
    position: relative;
    overflow: hidden;
    margin: 0 0 20px;
    .control-nav {
      display: block;
      position: absolute;
      top: 50%;
      width: 64px;
      height: 64px;
      margin: -32px 0 0;
      cursor: pointer;
      opacity: 0;
      @include t(opacity);
      @media(max-width: 480px){
        @include transform(scale(0.7));
      }
      &-prev {
        left: 0;
        background: url('../img/icon-76.png') no-repeat 50% 50%;
      }

      &-next {
        right: 0;
        background: url('../img/icon-77.png') no-repeat 50% 50%;
      }
    }

    .control {
      display: inline-block;
      width: 40px;
      height: 40px;
      margin: 20px 16px;
      cursor: pointer;
      @media(max-width: 480px){
        @include transform(scale(0.7));
        margin: 5px 16px;
      }
      &-wrap {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 80px;
        background: rgba(0,0,0,0.3);
        text-align: center;
        opacity: 0;
        @include t(opacity);
        @media(max-width: 480px){
          height: 45px;
        }
      }

      &-1 {
        background: url('../img/icon-78.png') no-repeat 50% 50%;
      }

      &-2 {
        background: url('../img/icon-79.png') no-repeat 50% 50%;
      }

      &-3 {
        background: url('../img/icon-80.png') no-repeat 50% 50%;
      }
    }

    &:hover {

      .control-nav,
      .control-wrap {
        opacity: 1;
      }
    }
  }

  &-list {
    overflow: hidden;
    margin: 0 -20px -20px 0;

    .unit {
      display: block;
      float: left;
      position: relative;
      width: 25%;
      padding: 0 20px 20px 0;

      @include media('lg') {
        width: 100% / 3;
      }

      @include media('md') {
        width: 50%;
      }

      @include media('sm') {
        width: 100%;
      }

      .img {
        display: block;
        width: 100%;
      }

      .control {
        display: block;
        float: left;
        width: 60px;
        height: 60px;
        cursor: pointer;

        &-wrap {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 180px;
          height: 60px;
          margin: -40px 0 0 -100px;
          background: rgba(255,255,255,0.5);
          overflow: hidden;
          opacity: 0;
          @include t(opacity);
        }

        &-1 {
          background: url('../img/icon-81.png') no-repeat 50% 50%;
        }

        &-2 {
          background: url('../img/icon-82.png') no-repeat 50% 50%;
        }

        &-3 {
          background: url('../img/icon-83.png') no-repeat 50% 50%;
        }
      }

      &:hover {

        .control-wrap {
          opacity: 1;
        }
      }
    }
  }
}
