.button-wrap .btn{
  min-width: 100%;
  width: 100%;
  line-height: 80px;
  margin-bottom: 20px;
}
.menu-dialog {
  a{
    width: 25%;
    height: 180px;
    line-height: 180px;
    color: #fff;
    background: $brand-danger;
    font-size: 26px;
    display: block;
    float: left;
    margin-bottom: 0;
    border: 1px solid $brand-danger-hue;
    &:hover{
      background: $brand-danger-hue;
      text-decoration: none;
    }
    @media(max-width: 1080px){
      width: 33%;
    }
    @media(max-width: 768px){
      width: 50%;
    }
    @media(max-width: 480px){
      width: 100%;
    }
  }
}

.menu-dialog:before, .menu-dialog:after { content: ''; display: table; }
.menu-dialog:after { clear: both; }

.menu-dialog > div {
  flex: auto;
  text-align: left;
  margin: 0.25em;
  padding: 2em 3em;
  background: rgba(255,255,255,0.2);
}

.menu-dialog h2 {
  margin: 0 0 0.25em;
  padding: 0 0 0.25em;
  letter-spacing: 1px;
  text-transform: uppercase;
  font-size: 72.25%;
  color: #9BA890;
}

.menu-dialog ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.menu-dialog ul:not(:last-of-type) {
  padding-bottom: 2em;
}

.menu-dialog ul li a {
  display: block;
  font-weight: bold;
  padding: 0.25em 0;
  font-size: 145%;
}

.menu-dialog ul li a.current-demo {
  color: $brand-info;
}

.dialog,
.dialog__overlay {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99999;
}

.dialog {
  position: fixed;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  pointer-events: none;
}

.dialog__overlay {
  position: absolute;
  z-index: 1;
  background: rgba(55, 58, 71, 0.9);
  opacity: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  -webkit-backface-visibility: hidden;
}

.dialog--open .dialog__overlay {
  opacity: 1;
  pointer-events: auto;
}

.dialog__content {
  width: 50%;
  max-width: 560px;
  min-width: 290px;
  background: #fff !important;
  padding: 4em !important;
  text-align: center;
  position: relative;
  z-index: 5;
  opacity: 0;
}

.dialog--open .dialog__content {
  pointer-events: auto;
}

/* Content */
.dialog h2 {
  margin: 0;
  font-weight: 400;
  font-size: 2em;
  padding: 0 0 2em;
  margin: 0;
}
